
import ListItem from '../components/ListItem'

function SecretListPage({ secrets, onItemSelect }) {
  return (
    <>
      {
        secrets.map(secret => <ListItem
          title={secret.name}
          subTitle={secret.username}
          key={secret.id}
          id={secret.id}
          onClick={onItemSelect}
        />)
      }
    </>
  )
}

export default SecretListPage;