
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import { Box, Typography, IconButton, Link } from '@mui/material';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { useState } from 'react'
import { useTheme } from '@mui/material/styles';

const hiddenText = '*********'

function SecretDetailField({ fieldName, val, hidden, link }) {
  const [passShow, setPassShow] = useState(false)
  const theme = useTheme()
  const isHidden = hidden === true
  const isLink = link === true

  return <Box mt={5} display="flex" style={{
    width: '100%',
    display: 'grid',
    gridTemplateColumns: 'auto 1fr',
    gridGap: 10
  }} >
    <Box>
      <Box>
        <Typography variant="subtitle1" color={theme.palette.text.primary} style={{ wordBreak: 'break-word' }}>
          {fieldName}
        </Typography>
      </Box>
      <Box>
        {
          isLink ?
            <Link href={val} target="_blank">{val}</Link> :
            <Typography variant="h7" color={theme.palette.text.secondary} style={{ wordBreak: 'break-word' }}>
              {isHidden ? (passShow ? val : hiddenText) : val}
            </Typography>
        }

      </Box>
    </Box>
    <Box display="flex" pt={2}>
      {
        isHidden ?
          <Box>
            <IconButton
              aria-label="toggle password visibility"
              onClick={() => setPassShow(!passShow)}
              onMouseDown={(e) => e.preventDefault()}
              edge="end"
            >
              {passShow ? <VisibilityOff /> : <Visibility />}
            </IconButton>
          </Box> : null
      }
      <Box ml={4} pr={2}>
        <CopyToClipboard text={val}>
          <IconButton
            edge="end"
          >
            <ContentCopyIcon />
          </IconButton>
        </CopyToClipboard>
      </Box>
    </Box>
  </Box>
}

export default SecretDetailField;