
import { Box, Snackbar, Alert } from '@mui/material'
import { useState, useEffect } from 'react'
import Container from '@mui/material/Container'
import Auth from './pages/Auth'
import Auth2 from './pages/Auth2'
import { useTheme } from '@mui/material/styles';
import CryptoJS from 'crypto-js';
import { endpoint, makeid, toDetailed } from './globals'
import MainList from './pages/MainList'
import Loader from './components/Loader'

function Main() {
  const [searchFocus, setSearchFocus] = useState(false)
  const [authStep, setAuthStep] = useState(1)
  const [showError, setShowError] = useState(false)
  const [f, setF] = useState('')
  const [loading, setLoading] = useState(false);
  const [mainData, setMainData] = useState(null);
  const theme = useTheme()

  function doc_keyUp(e) {
    if (authStep !== 3) return;
    if (e.ctrlKey && e.code === 'KeyF' && e.shiftKey) {
      e.preventDefault();
      setSearchFocus(true)
    }
  }

  useEffect(() => {
    document.addEventListener('keydown', doc_keyUp, false);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const onFirstAuth = (password) => {
    const hash = createHash(password)

    const encrypted = CryptoJS.AES.encrypt(`${hash}↑${makeid(5)}↑${(new Date()).getTime()}`, "9wdff0j203j4nf02234f23f");
    const f = encrypted.toString()
    setF(f)
    setLoading(true)
    serverAction(f).then(result => {
      if (result.isValid)
        setAuthStep(authStep + 1)
      else
        setShowError(true)
      setLoading(false)

    }).catch(e => {
      setShowError(true)
      setLoading(false)
    })
  }

  const onSecondAuth = (password) => {
    const hash = createHash(password)

    const encrypted = CryptoJS.AES.encrypt(`${hash}↑${makeid(5)}↑${(new Date()).getTime()}`, "9wdff0j203j4nf02234f23f");

    setLoading(true)
    serverAction(f, encrypted.toString()).then(result => {
      if (result.isValid) {
        setAuthStep(authStep + 1)
        const key = CryptoJS.enc.Base64.parse(hash).toString(CryptoJS.enc.Hex).substr(0, 20)
        let parsed = null;
        const decrypted = CryptoJS.AES.decrypt(result.data, key)
        try {
          parsed = JSON.parse(decrypted.toString(CryptoJS.enc.Utf8))
        } catch (e) {

        }
        const detailed = toDetailed(parsed)
        setMainData(detailed)
      } else {
        setShowError(true)
      }
      setLoading(false)

    }).catch(e => {
      setShowError(true)
      setLoading(false)
    })
  }

  const createHash = (password) => {
    const hash = CryptoJS.SHA256(password)
    const bHash = CryptoJS.enc.Base64.stringify(hash)
    return bHash
  }

  const serverAction = async (hash, hash2) => {
    let obj = { hash: hash }
    if (hash2) {
      obj.hash2 = hash2
      obj.step2 = true
    }
    const rawResponse = await fetch(`${endpoint}/monlock_action`, {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(obj)
    });
    const content = await rawResponse.json();
    return content;
  }
  const logout = () => {
    setAuthStep(1)
    setF('')
    setMainData(null)
  }

  return <Box bgcolor={theme.palette.background.default}>
    <Container component="main" >
      <Box style={{ height: '100vh' }}>
        {
          authStep === 1 ? <Auth onAuth={onFirstAuth} /> : null
        }
        {
          authStep === 2 ? <Auth2 onAuth={onSecondAuth} /> : null
        }
        {
          authStep === 3 ? <MainList mainData={mainData} onLogout={logout} searchFocus={searchFocus} setSearchFocus={setSearchFocus} /> : null
        }

      </Box>
      {
        loading ? <Loader /> : null
      }
      <Snackbar
        anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
        open={showError}
        onClose={() => setShowError(false)}
      >
        <Alert onClose={() => setShowError(false)} severity="warning" sx={{ width: '100%' }}>
          Алдаа гарлаа!
        </Alert>
      </Snackbar>
    </Container>
  </Box>
}

export default Main;