
import { Box, Typography } from '@mui/material';
import { useTheme } from '@mui/material/styles';
import SecretDetailField from '../components/SecretDetailField'
import _ from 'lodash';

function SecretDetailPage({ secret }) {
  const hasPass = !!secret.password && _.isString(secret.password)
  const hasUrl = !!secret.url && _.isString(secret.url)
  const hasUserName = !!secret.username && _.isString(secret.username)
  const theme = useTheme()
  let addits = _.get(secret, 'additFields', [])
  if (!_.isArray(addits)) {
    addits = []
  }

  return (
    <Box mt={6}>
      <Typography variant="h6" color={theme.palette.text.primary}>
        {secret.name}
      </Typography>
      {hasUrl ? <SecretDetailField fieldName="URL" val={secret.url} link /> : null}
      {hasUserName ? <SecretDetailField fieldName="Username" val={secret.username} /> : null}

      {
        hasPass ?
          <SecretDetailField fieldName="Password" val={secret.password} hidden /> : null
      }
      {
        addits.map((addit, index, arr) => <SecretDetailField fieldName={addit.fieldName} val={addit.value} hidden key={index} />)
      }
    </Box>
  )
}

export default SecretDetailPage;