
import { Box, Autocomplete, TextField, Typography } from '@mui/material';
import _ from 'lodash';
import { useRef, useEffect } from 'react'

function SearchArea({ onFocusChange, secrets, onSelect }) {
  const textInput = useRef(null);
  useEffect(() => {
    if (textInput) {
      textInput.current.focus()
    }
  }, [textInput]);
  const filtering = (options, keyword) => {
    return options.filter(option => {

      if (option.name.toLowerCase().includes(keyword)) {
        return true;
      }
      if (option.username && option.username.toLowerCase().includes(keyword)) {
        return true;
      }
      if (option.url && option.url.toLowerCase().includes(keyword)) {
        return true;
      }
      if (_.isArray(option.additFields)) {
        for (const addit of option.additFields) {
          if (addit.fieldName.toLowerCase().includes(keyword)) {
            return true
          }
        }
      }
      return false
    })
  }
  return <Autocomplete
    freeSolo
    id="free-solo-2-demo"
    getOptionLabel={(option) => `${option.name}`}
    options={secrets}
    onChange={(event, newValue) => {
      onSelect(newValue.id)
    }}
    filterOptions={(options, state) => filtering(options, state.inputValue)}
    renderOption={(props, option) => (
      <Box component="li" sx={{ '& > img': { mr: 2, flexShrink: 0 } }} {...props}>
        <Box display="flex" flexDirection="column">
          <Box>
            <Typography variant="subtitle2">{option.name}</Typography>
          </Box>
          <Box>
            <Typography variant="caption">{option.username}</Typography>
          </Box>
        </Box>
      </Box>
    )}
    renderInput={(params) => (
      <TextField
        onFocus={() => onFocusChange(true)}
        onBlur={() => onFocusChange(false)}
        {...params}
        label="Хайх талбар"
        inputRef={textInput}
      />
    )}
  />
}

export default SearchArea;