
import { Box, Backdrop, Button, Container, IconButton } from '@mui/material';
import SearchArea from '../components/SearchArea'
import SecretListPage from './SecretListPage'
import SecretDetailPage from './SecretDetailPage'
import { useState } from 'react'
import { useTheme } from '@mui/material/styles';
import LogoutIcon from '@mui/icons-material/Logout';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import _ from 'lodash'

function MainList(props) {
  const [page, setPage] = useState('list')
  const [selectedSecret, setSelectedSecret] = useState(null)

  const theme = useTheme()
  let secrets = _.get(props, 'mainData.data.secrets', [])

  const onItemSelect = (id) => {
    props.setSearchFocus(false)
    setPage('detail')
    const chosenSecret = secrets.find(secret => secret.id === id)
    if (chosenSecret) {
      setSelectedSecret(chosenSecret)
    }
  }

  return (
    <Box pt={6} >
      <Box pb={3}>
        <Box display="flex">
          {
            page === "detail" ? <Box mr={2}>
              <IconButton onClick={() => setPage('list')}>
                <ArrowBackIcon />
              </IconButton>
            </Box> : null
          }

          <Button onClick={val => props.setSearchFocus(true)} fullWidth variant="outlined">
            Хайх
          </Button>

          <Box ml={2}>
            <IconButton
              onClick={() => props.onLogout()}
              onMouseDown={(e) => e.preventDefault()}
            >
              <LogoutIcon />
            </IconButton>
          </Box>
        </Box>
        {props.searchFocus ? <Backdrop
          sx={{ zIndex: (theme) => theme.zIndex.drawer + 1 }}
          style={
            {
              background: 'rgb(0 0 0 / 52%)',
              backdropFilter: 'blur(5px)',
              WebkitBackdropFilter: 'blur(5px)',
              alignItems: 'flex-start',
              paddingTop: theme.spacing(8)
            }
          }
          open={props.searchFocus}
        >
          <Container  >
            <Box px={7}>
              <SearchArea onFocusChange={val => props.setSearchFocus(val)} secrets={secrets} onSelect={onItemSelect} />
            </Box>
          </Container>
        </Backdrop> : null
        }

      </Box>
      <Box style={{ height: 'calc(100vh - 87px)', overflowY: 'auto' }} >
        {
          page === 'list' ? <SecretListPage secrets={secrets} onItemSelect={onItemSelect} /> : null
        }
        {
          page === 'detail' ? <SecretDetailPage secret={selectedSecret} /> : null
        }
      </Box>
    </Box >
  )
}

export default MainList;
