
export const endpoint = 'https://2cqhopp1d8.execute-api.ap-southeast-1.amazonaws.com/Prod'

export function makeid(length) {
  var result = '';
  var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  var charactersLength = characters.length;
  for (var i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() *
      charactersLength));
  }
  return result;
}


export function toDetailed(messyData) {

  let outData = {
    secrets: [],
    folders: []
  };
  try {
    messyData[3].forEach((messySecret) => {
      let newSecret = {
        id: messySecret[0],
        name: messySecret[1],
        url: messySecret[2] === 0 ? undefined : messySecret[2],
        username: messySecret[3] === 0 ? undefined : messySecret[3],
        password: messySecret[4] === 0 ? undefined : messySecret[4],
        additFields: [],
        folderID: messySecret[6]
      }
      if (messySecret[5] !== 0) {
        (messySecret[5]).forEach(messyAddit => {
          newSecret.additFields.push({
            fieldName: messyAddit[0],
            value: messyAddit[1]
          })
        })
      }
      outData.secrets.push(newSecret);
    });
    messyData[4].forEach((messyFolder) => {
      outData.folders.push({
        id: messyFolder[0],
        name: messyFolder[1],
        secrets: []
      });
    });
  } catch (e) {
    return { data: outData }
  }
  return {
    data: outData,
    stamp: messyData[0]
  };
}